@font-face {
    font-family: 'Space Ranger Title';
    src: url(/tp/static/media/SpaceRangerTitle.8b44bfa5.woff2) format('woff2'),
    url(/tp/static/media/SpaceRangerTitle.a0e093f7.woff) format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    overflow-x: hidden;
}

body {
    color: #fff;
    background-color: #000;
    font-family: 'Trebuchet MS', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: normal;
    font-style: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1 {
    font-family: 'Space Ranger Title', monospace;
    font-weight: normal;
    font-style: normal;
}

em {
    text-decoration: underline;
}

#root {
    position: fixed;
    width: 100%;
    padding: 0;
    z-index: 1;
}

button {
    font-family: 'Space Ranger Title', monospace;
    box-shadow: 0 0 10px #00dcdc;
    background: #00bebe;
    color: #212121;
    font-size: 4vh;
    width: 100%;
    height: 10vh;
    border: none;
    padding: 1vh 0;
}

button:not(:last-child) {
    margin-bottom: 2vh;
}

button.not-important {
    color: #777777;
    background: #212121;
    box-shadow: none;
}
.dialog {
    width: 90%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow-y: scroll;
}

.dialog-inner {
    min-height: calc(100% - 2rem);
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.dialog-content {
    flex: 1 1;
    padding-bottom: 1rem;
}
.detailed-planet-image {
    width: 50vw;
    height: 50vw;
}



.depth-level-1,
.depth-level-2,
.depth-level-3,
.depth-level-4,
.depth-level-5,
.depth-level-6,
.depth-level-7,
.depth-level-8,
.depth-level-9,
.depth-level-10,
.depth-level-excavated {
    float: left;
    width: 29%;
    margin-left: 2%;
    margin-right: 2%;
    padding: 0;
    text-align: center;
    height: 80px;
    line-height: 80px;
    background-color: #565656;
    color: white;
    font-size: 12px;
    text-decoration: none;
    box-shadow: 0 0 10px #333333;
}
