.depth-level-2  { background-color: #606060; }
.depth-level-3  { background-color: #565656; }
.depth-level-4  { background-color: #505050; }
.depth-level-5  { background-color: #464646; }
.depth-level-6  { background-color: #404040; }
.depth-level-7  { background-color: #363636; }
.depth-level-8  { background-color: #303030; }
.depth-level-9  { background-color: #262626; }
.depth-level-10 { background-color: #202020; }*/
.depth-level-excavated {
    background-color: #222;
    border-color: #111;
    color: #999; font-style: italic; line-height: 0.2em;}


img.icon {
    width: 16px;
    height: 16px;
    vertical-align: bottom;
}

span.icon {
    /*font-family: 'Space Ranger Title', monospace;
    font-weight: normal;*/
    font-style:italic;
}
.data-log {
    padding-top: 1em;
    padding-bottom: 1em;
    width: 100%;
    background-color: #565656;
    margin: auto auto 2rem;
}

.data-log img {
    height: 30vh;
}
.genesis-button {
    float: left;
    width: 29%;
    margin-left: 2%;
    margin-right: 2%;
    padding: 0;
    text-align: center;
    height: 80px;
    line-height: 80px;
    background-color: #565656;
    color: white;
    font-size: 12px;
    text-decoration: none;
    box-shadow: 0 0 10px #333333;
}
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.scanning-planet-image {
    width: 2em;
    height: 2em;
}

.scanner-overlay, .scanner-static-background {
    position: relative;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
}

.scanner-background {
    position: relative;
    top: 0%;
    width: 100%;
    height: 100%;
    background-size: 50vw;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: 1rem;
}

.scanner-background, .scanner-static-background {
    z-index: 1;
}

.scanner-info {
    z-index: 10;
}

.scanner-overlay, .scanner-overlay-locked {
    z-index: 100;
}

.scanner-static-background {
    opacity: 1.0;
    background-image: url(/tp/static/media/scanner-interlace-01.b46b239c.png);
    background-size: 100% 100%;
}

.scanner-mode-locking > .scanner-background {
    background-color: black;/*#2471ff*/
}

.scanner-mode-locked > .scanner-background {
    opacity: 0.8;
    background-color: black;/*#007980;*/
}

.scanner-mode-explored > .scanner-background {
    opacity: 0.8;
    background-color: darkred;
}

.hidden-canvas {
    display: none;
}

.menu-button img {
    z-index: 200;
    width: 100%;
    height: 100%;
    font-size: 2em;
}

.scanner-background {
    position: absolute;
    width: 100%;
    height: 100%;
}

.scanner-static-background {
    position: absolute;
    width: 100%;
    height: 100%;
}

.scanner-info h1 {
    margin: 0;
    padding: 0;
    padding-top: 0.7rem;
    font-size: 2.0rem;
}

button.menu-button {
    z-index: 200;
    box-shadow: none;
    position: absolute;
    right: 0;
    width: 2.5rem;
    height: 2.5rem;
    padding: 0;
    margin: 0.3rem;

    background-color: transparent;
    border: none;
}

button.planet-link {
    float:left;
    display:block;
    margin: 1vw;
    padding: 0;
    width: 18vw;
    height: 18vw;
}

.planet-link img {
    width: 100%;
    height: 100%;
}
